.main {
  background-color: $main-bcg-light;
  color: $title-crl-onlight;

  padding-bottom: 80px;

  @include screens($tablet) {
    padding-bottom: 130px;
  }

  @include screens($desktop) {
    padding-bottom: 200px;
  }

  &__how-to-visit {
    background-color: $main-bcg-dark;
    padding: 50px 20px;
    margin: 0 -20px;

    @include screens($tablet) {
      margin: 0 -40px;
      padding: 80px 40px;
    }

    @include screens($desktop) {
      margin: 0 -55px;
      padding: 205px 55px;

      background-image: url("../images/img_07.png");
      background-repeat: no-repeat;

    }
  }

  &__container {
    background-color: $main-bcg-light;
  }

  &__section {
    padding-top: 90px;

    &:first-child {
      @include screens($tablet) {
        padding-top: 105px;
      }

      @include screens($desktop) {
        padding-top: 110px;
      }
    }

    &:nth-child(2) {

      @include screens($tablet) {
        padding-top: 160px;
      }

      @include screens($desktop) {
        padding-top: 200px;
      }
    }

    &:nth-child(3) {
      @include screens($tablet) {
        padding-top: 110px;
      }

      @include screens($desktop) {
        padding-top: 100px;
      }
    }

    &:nth-child(3) {
      @include screens($tablet) {
        padding-top: 125px;
      }

      @include screens($desktop) {
        padding-top: 110px;
      }
    }

    &:nth-child(4) {
      @include screens($tablet) {
        padding-top: 160px;
      }

      @include screens($desktop) {
        padding-top: 200px;
      }
    }
  }

  &__section-title {
    display: flex;
    padding: 0;
    margin: 0;

    font-family: "Playfair Display", serif;
    font-weight: 700;
    font-size: 28px;
    line-height: 35px;

    color: $title-crl-onlight;

    @include screens($tablet) {
      display: inline;
      font-size: 40px;
      line-height: 50px;
    }

    @include screens($desktop) {
      font-size: 48px;
      line-height: 58px;
    }

    &--ondark {
      display: block;
      margin: 0;
      font-size: 38px;
      line-height: 38px;

      color: $titletext-clr-ondark;

      @include screens($tablet) {
        height: 3em;
        font-size: 62px;
        line-height: 62px;
      }


      @include screens($desktop) {
        font-size: 80px;
        line-height: 80px;
      }
    }

    &--news {
      font-size: 20px;
      line-height: 25px;

      @include screens($tablet) {
        font-size: 20px;
        line-height: 30px;
      }
    }
  }

  &__card {
    display: flex;
    flex-direction: column;

    margin: 60px 0 0;

    &--events-card {
      @include screens($tablet) {
        flex-direction: row;
        gap: 30px;
      }
    }
  }


  &__img {
    background-image: center;
    object-fit: cover;
    height: 356px;
    display: block;
    border: 0;
    margin: 0;
    padding: 0;
    outline: 0;

    @extend %basic-transition;
    cursor: pointer;

    @include screens($tablet) {
      height: 420px;
    }

    @include screens($desktop) {
      height: 480px;
    }

    &--news {
      height: 280px;

      @include screens($tablet) {
        height: 330px;
      }

      @include screens($desktop) {
        height: 370px;
      }
    }

    &:hover {
      transform: scale(0.9);
      box-shadow: 3px 3px 12px rgb(91, 91, 91);
    }
  }

  &__date {
    margin: 20px 0 0;
    color: $btn-bcg;

    @include screens(768px) {
      margin-top: 30px;
    }

    &--grey {
      color: $title-crl-onlight;
    }
  }

  &__description {
    margin: 10px 0 0;
    font-family: "Playfair Display", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;

    @include screens($tablet) {
      height: 2em;
      font-size: 24px;
      line-height: 30px;
    }
  }

  &__paragraph {
    margin: 20px 0 0;
    color: $text-clr;

    @include screens($tablet) {
      height: 3em;
    }

    &--ondark {
      background-color: $main-bcg-dark;
      color: $titletext-clr-ondark;

      @include screens($tablet) {
        margin-top: -50px;
      }

      @include screens($desktop) {
        margin-top: -60px;
      }

    }
  }

  &__button {
    margin-top: 30px;

    @include screens($tablet) {
      margin-top: 60px;
    }

    @include screens($desktop) {
      margin-top: 70px;
    }
  }

  &__info {
    display: flex;
    flex-direction: column;

    @include screens($tablet) {
    margin: -30px 0;
    }
  }

  &__input {
    width: 100%;
    margin-top: 50px;

    font-family: 'Raleway';
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;


    outline: none;
    border: none;
    border-bottom: 1px solid $title-crl-onlight;
    background-color: $main-bcg-light;

    transition: all 0.3s ease;

    &::placeholder {
      color: $title-crl-onlight;
    }

    &:hover::placeholder {
      color: $btn-act;
    }

    &:hover {
      border-bottom: 1px solid $btn-act;
    }

    &:focus {
      color: $btn-act;
      border-bottom: 1px solid $btn-act;
    }

    &:required:valid {
      font-weight: 700;
      border-bottom: 1px solid $title-crl-onlight;
    }
  }

  &__text-breaker {
    display: none;

    @include screens($desktop) {
      display: block;
    }
  }
}
