.button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;

  text-decoration: none;

  line-height: 16px;
  font-weight: 600;



  @extend %basic-transition;

  cursor: pointer;

  @include screens($desktop) {
    width: 270px;;
  }

  color: $titletext-clr-ondark;
  border: 1px solid transparent;

  &--empty {
    border: 1px solid $btn-bcg;
    color: $btn-bcg;
    margin-top: 30px;

    @include screens($tablet) {
      width: auto;
      display: none;
    }

    &:visited {
      border: 1px solid $btn-vis;
      color: $btn-vis;
    }



    &:hover {
      border: 1px solid $btn-hov;
      color: $btn-hov;
      background-color: rgba(252, 252, 252, 0.3);
    }

    &:active {
      border: 1px solid $btn-act;
      color: $btn-act;
    }
  }

  &--filled {
    background-color: $btn-bcg;
    @extend %basic-transition;

    &:hover {
      background-color: $btn-hov;
    }

    &:active {
      transform: scale(0.7);
      background-color: $btn-act;
    }

    &:visited {
      background-color: $btn-vis;
    }
  }

  &__arrow {
    margin-left: 18px;

    &--bottom {
      display: flex;
      justify-content: center;
      margin: 0;
      font-size: 35px;
      transform: rotate(-90deg);
    }
  }

  &--top {
    display: none;
    max-width: auto;

    @include screens($tablet) {
      display: flex;
      margin: 0;
    }
  }

  &--ondark {
    margin-top: 40px;

    @include screens($tablet) {
      margin-top: 80px;
    }

    @include screens($desktop) {
      margin-top: 50px;
    }
  }

  &__link {
    text-decoration: none;
    padding: 5px;
    color: $titletext-clr-ondark;

    @extend %basic-transition;

    &:hover {
      text-shadow: 0 3px 5px $title-crl-onlight;
     }
  }

  &--submit {
    width: 100%;
    display: block;
    font-family: 'Raleway';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
  }
}



