.nav {
  display: flex;
  justify-content: space-between;
  height: 60px;
  align-items: center;

  @include screens($tablet) {
    height: 75px;
  }

  @include screens($desktop) {
    height: 90px;
  }

  &__side {
    display: flex;
    align-items: center;
    gap: 40px;

    @include screens($tablet) {
      flex-direction: row-reverse;
    }
  }

  &__icon {
    width: 30px;
    height: 30px;
    transition: all 0.3s;
    cursor: pointer;

    &--burger:hover {
      transform: scaleY(0.5);
    }

    &--cross {
      border-radius: 0;
      @extend %basic-transition;

      &:hover {
        transform: rotate(360deg) scale(0.7);
      }
    }
  }

  &__btn--dropdown {
    line-height: 24px;
    position: relative;
    font-weight: 500;
    color: $titletext-clr-ondark;
    text-decoration: none;

    &::after {
      content: "";
      position: absolute;
      display: block;
      width: 4px;
      height: 4px;
      bottom: 50%;
      left: 110%;

      border-right: 2px solid $titletext-clr-ondark;
      border-bottom: 2px solid $titletext-clr-ondark;
      transform: rotate(45deg) translateX(50%);
      transform-origin: 4px 4px;
      transition: 0.2s;
    }

    &:hover::after {
      transform: rotate(-135deg);
    }

    &::before {
      content: "EN";
      position: absolute;
      font-size: 16px;
      color: $titletext-clr-ondark;
      transform: translate(0, 90%);
      z-index: -2;
      opacity: 0;

      text-shadow: 0 2px 3px rgba(#343434, 1);
      transition: 0.7s ease-in-out;
    }

    &:hover::before {
      opacity: 1;
      z-index: 0;
      color: $titletext-clr-ondark;
    }
  }

  &__btn--desktop {
    display: none;

    @include screens($tablet) {
      display: block;
    }
  }
}
