.header {
  height: 100vh;

  background-image: url("../images/bgc/img_01.png");
  background-repeat: no-repeat;
  background-position: 50% 100%;
  background-size: 50%;

  @include screens($tablet) {
    background-size: 440px;
  }

  @include screens(1024px) {
    background-position: 100% 100%;
    background-size: 45%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    margin-top: 70px;
    padding-bottom: 315px;

    @include screens($tablet) {
      padding-bottom: 550px;
    }

    @include screens($desktop) {
      padding-bottom: 200px;
    }
  }

  &__events {
    margin: 0;
    font-size: 14px;
    line-height: 20px;
    color: $titletext-clr-ondark;
  }

  &__title {
    margin: 10px 0 0;
    font-size: 48px;
    line-height: 48px;

    @include screens($tablet) {
      font-size: 72px;
      line-height: 72px;
    }

    @include screens($desktop) {
      font-size: 96px;
      line-height: 96px;
    }
  }

  &__info {
    margin: 30px 0 0;

    @include screens($tablet) {
      margin-top: 20px;
    }

    @include screens($desktop) {
      margin-top: 30px;
    }
  }

  &__button {
    margin-top: 40px;
    box-shadow: 3px 3px 3px rgba(#232323, 0.7);

    @include screens($tablet) {
      margin-top: 50px;
    }

    @include screens($desktop) {
      margin-top: 80px;
    }
  }

  &__dates {
    display: none;
    margin: 15vh 0 0;
    font-weight: 700;
    font-size: 14px;
    line-height: 18px;
    font-family: 'Playfair Display';

    @include screens($desktop) {
      display: block;
    }
  }

  &__decor {
    display: none;
    height: 1px;
    background-color: $titletext-clr-ondark;
    transform: translateY(-700%);

    @include screens($desktop) {
      display: block;
    }
  }
}
