.menu {
  height: 100vh;
  padding-bottom: -135px;

    @include screens($tablet) {
    background-image: url("../images/img_menu.png");
    background-repeat: no-repeat;
    background-position: 50% 100%;
    background-size: 440px;
  }

  @include screens(1024px) {
    background-size: 45%;
    background-position: 100% 100%;
  }

  &__content {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
  }

  &__link {
    box-sizing: border-box;
    display: block;
    width: max-content;
    text-decoration: none;

    color: $titletext-clr-ondark;
    border-bottom: 1px solid transparent;

    &:hover {
      border-bottom: 1px solid $titletext-clr-ondark;
    }
  }

  &__info {
    @include screens($tablet) {
      display: flex;
      gap: 120px;
    }
  }

  &__container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transform: translateY(-120%);
    opacity: 0.7;

    background-color: $main-bcg-dark;
    transition: transform 0.3s ease;

    &--open {
      opacity: 1;
      transform: translateY(0);
    }
  }

  &__title {
    margin: 0;

    font-weight: 400;
    font-size: 14px;
    line-height: 20px;

    &:not(:first-child) {
      margin: 20px 0 0;
    }
  }

  &__details {
    margin: 0;
    font-weight: 600;
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 20px;

    margin: 40px 0 0;
    padding: 0;

    font-family: "Playfair Display", serif;
    font-weight: 700;
    font-size: 20px;
    line-height: 34px;
    list-style-type: none;

    border-bottom: 1px solid $titletext-clr-ondark;

    @include screens($tablet) {
      margin-top: 50px;
      padding-bottom: 50px;
      gap: 30px;
      font-size: 48px;
      line-height: 50px;
    }

    @include screens($desktop) {
      gap: 60px;
      padding-bottom: 60px;
      margin-top: 70px;
    }
  }

  &__geo {
    @extend .menu__details;
    text-decoration: none;
    color: $titletext-clr-ondark;
    @extend %basic-transition;

    &:hover {
      opacity: 0.8;
    }

  }
}
