.namu-footer {
  background-color: $main-bcg-dark;
  height: 300px;
  padding: 30px 0;

  @include screens($tablet) {
    padding-top: 50px;
  }

  @include screens($tablet) {
    display: grid;
    grid-template-columns: 2fr 2fr 2fr;
    gap: 30px;
  }

  @include screens($desktop) {
    display: grid;
    grid-template-columns: 4fr 4fr 4fr;
    gap: 30px;
  }

  &__block {
    display: flex;
    flex-direction: column;

    &:nth-child(n + 2) {
      margin-top: 50px;

      @include screens($tablet) {
        margin-top: 0;

      }
    }

    &:nth-child(n + 3) .namu-footer__title {
      font-size: 20px;
    }
  }

  &__social {
    display: flex;
    gap: 20px;

    margin-top: 30px;

    @include screens($tablet) {
      margin-top: 50px;
    }
  }

  &__icons {
    width: 20px;
    height: 20px;
    opacity: 0.3;

    @extend %basic-transition;

    @for $index from 1 through 4 {
      &--#{$index} {
        background-image: url("../images/icons/#{$index}.svg");
      }
    }

    &:hover {
      opacity: 1;
    }
  }

  &__title {
    margin: 0 0 10px;
    font-family: "Playfair Display";
    font-weight: 700;
    font-size: 18px;
    line-height: 27px;
    text-shadow: 0 4px 3px black;

    @include screens($tablet) {
      margin-bottom: 30px;
      font-size: 20px;
      line-height: 30px;

    }
  }

  &__content {
   margin: 0;
  }

  &__menu {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  &__list {
    margin: 0;
    padding: 0;
  }

  &__link {
    text-decoration: none;
    color: $titletext-clr-ondark;


    @extend %basic-transition;

    @include screens($tablet){
      padding: 10px 0;
      margin-top: 10px;
    }

    &:hover {
      text-shadow: 0 3px 3px $title-crl-onlight;
    }
  }

  &__bottom {
    display: flex;
    justify-content: space-between;
    grid-column: 1 / 7;
    margin-top: 60px;

    font-size: 14px;
  }

  &__info {
    display: flex;
    gap: 20px;

    @include screens($desktop) {
      gap: 100px;
    }
  }

  &__terms {
    cursor: pointer;
    @extend %basic-transition;

    &:last-child {
      margin-right: 10px;
    }

    &:hover {
      text-decoration: underline;
    }
  }
}
